.container {
  position: absolute;
  background: #b3d571;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
}

.header {
  text-align: center;
  font-family: 'Rubik Microbe';
}
.imageBlock {
  width: 100%;
  justify-self: center;
}

.button {
  background-color: #5fac5a;
  font-weight: bold;
  font-size: 1.5rem;
  width: 50%;
  align-self: center;
  border-radius: 5px;
  font-family: 'Rubik Microbe';
}

.name {
  color: #16dae7;
  text-shadow: 1px 1px 1px black;
}
