.container {
  position: relative;
  transform: rotate(-90deg);
  width: 50%;
  height: 100%;
}

.eyes {
  position: absolute;
  width: 170%;
  height: 100%;
  display: flex;
  left: -35%;
  top: 10%;
  z-index: 10;
}
.nose {
  height: 10%;
  position: absolute;
  bottom: 20%;
  width: 100%;
  display: flex;
  padding: 0% 30%;
  justify-content: space-between;
}
.nose::before,
.nose::after {
  content: '';
  display: block;
  width: 30%;
  height: 100%;
  background: black;
  border-radius: 50%;
}
.noseContainer {
  width: 100%;
  height: 85%;
  background: #16dae7;
  border-radius: 50%;
  transform: translate(0px, 15%);
  position: relative;
  z-index: 8;
}
.mouth {
  background: red;
  position: absolute;
  width: 75%;
  height: 60%;
  border-radius: 50%;
  top: 33%;
  left: 14%;
  z-index: 7;
}

.noseContainer__animateChew {
  animation: chew 1s infinite;
}
.tongue {
  background: rgb(140, 0, 0);
  width: 10%;
  height: 60%;
  position: absolute;
  bottom: 20%;
  left: 48%;
}

.tongue::after,
.tongue::before {
  content: '';
  display: block;
  width: 100%;
  height: 20%;
  background: rgb(140, 0, 0);
  position: absolute;
  top: 92%;
}
.tongue::after {
  transform: rotate(45deg);
  left: -100%;
}
.tongue::before {
  transform: rotate(-45deg);
  left: 100%;
}

.tongue__animate {
  animation: tongue 1s;
}

@keyframes tongue {
  0% {
    bottom: 0%;
  }
  20% {
    bottom: -58%;
  }
  40% {
    bottom: -30%;
  }
  50% {
    bottom: -58%;
  }
  60% {
    bottom: -20%;
  }
  70% {
    bottom: -45%;
  }
  80% {
    bottom: -58%;
  }
  90% {
    bottom: -25%;
  }
  95% {
    bottom: -58%;
  }
  100% {
    bottom: 20%;
  }
}

@keyframes chew {
  0% {
    transform: translate(0px, 15%) rotate3d(1, 0, 0, 0deg);
  }
  50% {
    transform: translate(0px, 15%) rotate3d(1, 0, 0, 65deg);
  }
  100% {
    transform: translate(0px, 15%) rotate3d(1, 0, 0, 0deg);
  }
}

.noseContainer__animateOpenMouth {
  transform: translate(0px, 15%) rotate3d(1, 0, 0, 65deg);
}

.neck {
  background: #16dae7;
  height: 50%;
  width: 100%;
  position: absolute;
}

.face {
  border-radius: 50%;
  background: #16dae7;
  height: 100%;
  position: relative;
  transform: rotate(0deg);
  z-index: 8;
}

.spiral {
  position: absolute;
  z-index: 12;
  top: 15%;
  left: -15%;
  height: 72%;
  opacity: 0.4;
  animation: spiral 0.7s infinite;
}

@keyframes spiral {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: 360deg;
  }
}

.face__animate {
  animation: face__animate 0.5s infinite;
}

@keyframes face__animate {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(20deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
