.container {
  border-radius: 50%;
  width: 100%;
  height: 50%;
  background-color: #16dae7;
  display: flex;
  justify-content: center;
  align-items: center;
}
.eye {
  border-radius: 50%;
  width: 70%;
  height: 70%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border: 1px solid rgb(19, 124, 156);
}

.eyeBall {
  width: 60%;
  height: 60%;
  background: green;
  border-radius: 50%;
  border: 1px solid black;
}

.lashes {
  position: absolute;
  width: 50%;
  height: 20%;
  display: flex;
  top: -7%;
  justify-content: space-between;
}

.lashe {
  width: 0.8%;
  height: 100%;
  background-color: black;

  position: absolute;
}
.lashe:nth-child(1) {
  transform: rotate(333deg);
  top: -3%;
  left: 30%;
}
.lashe:nth-child(2) {
  top: -3%;
  left: 25%;
  transform: rotate(145deg);
}
.lashe:nth-child(3) {
  left: 47%;
  top: -4%;
}
.lashe:nth-child(4) {
  left: 74%;
  top: -1%;
  transform: rotate(200deg);
}

.lashe:nth-child(5) {
  left: 97%;
  top: 3%;
  transform: rotate(219deg);
}
