.container {
  position: fixed;
  top: 0;
  left: 0;
  background: #00000081;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table {
  font-family: ;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  height: 10%;
  background-color: #b3d571;
  border-radius: 5px;
}

.table button {
  background-color: #5fac5a;
  font-weight: bold;
  width: 50%;
  align-self: center;
  border-radius: 5px;
  font-family: 'Rubik Microbe';
  font-size: 1rem;
}

.text {
  font-size: 1rem;
  margin-bottom: 5%;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
